article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  -webkit-text-size-adjust: none;
}

mark {
  background-color: transparent;
  color: inherit;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input,
select,
textarea {
  -moz-appearance: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

/* Basic */

.PresentationPage {
  box-sizing: border-box;
  color: #888;
  font-family: "Lato", sans-serif;
  font-size: 16pt;
  font-weight: 400;
  line-height: 1.75em;
  background: #fff;
}

.PresentationPage html {
  box-sizing: border-box;
}

.PresentationPage *,
.PresentationPage *:before,
.PresentationPage *:after {
  box-sizing: inherit;
}

.PresentationPage body.is-preload *,
.PresentationPage body.is-preload .PresentationPage *:before,
.PresentationPage body.is-preload .PresentationPage *:after {
  -moz-animation: none !important;
  -webkit-animation: none !important;
  -ms-animation: none !important;
  animation: none !important;
  -moz-transition: none !important;
  -webkit-transition: none !important;
  -ms-transition: none !important;
  transition: none !important;
}

.PresentationPage input,
.PresentationPageselect,
.PresentationPage textarea {
  color: #888;
  font-family: "Lato", sans-serif;
  font-size: 16pt;
  font-weight: 400;
  line-height: 1.75em;
}

.PresentationPage a {
  -moz-transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  -webkit-transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  -ms-transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  color: inherit;
  text-decoration: none;
}

.PresentationPage a:hover {
  border-bottom-color: transparent;
  color: #339966 !important;
}

.PresentationPage strong,
.PresentationPage b {
  color: #404040;
  font-weight: 700;
}

.PresentationPage em,
.PresentationPage i {
  font-style: italic;
}

.PresentationPage p {
  margin: 0 0 2.25em 0;
}

.PresentationPage h1,
.PresentationPage h2,
.PresentationPage h3,
.PresentationPage h4,
.PresentationPage h5,
.PresentationPage h6 {
  color: #404040;
  font-weight: 700;
  line-height: 1em;
  margin: 0 0 0.5625em 0;
}

.PresentationPage h1 a,
.PresentationPage h2 a,
.PresentationPage h3 a,
.PresentationPage h4 a,
.PresentationPage h5 a,
.PresentationPage h6 a {
  border: 0;
  color: inherit;
  text-decoration: none;
}

.PresentationPage h2 {
  font-size: 2em;
  line-height: 1.5em;
}

.PresentationPage h3 {
  font-size: 1.75em;
  line-height: 1.5em;
}

.PresentationPage h4 {
  font-size: 1.25em;
  line-height: 1.5em;
}

.PresentationPage h5 {
  font-size: 0.9em;
  line-height: 1.5em;
}

.PresentationPage h6 {
  font-size: 0.7em;
  line-height: 1.5em;
}

.PresentationPage sub {
  font-size: 0.8em;
  position: relative;
  top: 0.5em;
}

.PresentationPage sup {
  font-size: 0.8em;
  position: relative;
  top: -0.5em;
}

.PresentationPage hr {
  border: 0;
  border-bottom: solid 2px #f4f4f4;
  margin: 2.25em 0;
}

.PresentationPage hr.major {
  margin: 3.375em 0;
}

.PresentationPage blockquote {
  border-left: solid 8px #e4e4e4;
  font-style: italic;
  margin: 0 0 2.25em 0;
  padding: 0.5em 0 0.5em 2em;
}

.PresentationPage code {
  background: #555;
  border-radius: 5px;
  color: #fff;
  font-family: "Source Code Pro", monospace;
  font-size: 0.9em;
  margin: 0 0.25em;
  padding: 0.25em 0.65em;
}

.PresentationPage pre {
  font-family: "Source Code Pro", monospace;
  font-size: 0.9em;
  margin: 0 0 2.25em 0;
}

.PresentationPage pre code {
  -webkit-overflow-scrolling: touch;
  display: block;
  line-height: 1.5em;
  overflow-x: auto;
  padding: 1em 1.5em;
}

.align-left {
  text-align: left;
}

.align-center {
  text-align: center;
}

.align-right {
  text-align: right;
}

