    display: block;
    line-height: 1.75em;
    margin: 0.75em 0 0 0;
    padding-left: 0;
  }

  #footer .copyright li:first-child {
    margin-top: 0;
  }

  /* Header Panel */

  #titleBar .toggle {
    height: 4em;
    width: 4em;
  }

  #titleBar .toggle:before {
    font-size: 14px;
    width: 44px;
  }

  .header-visible #wrapper,
  .header-visible #titleBar {
    -moz-transform: translateX(-17em);
    -webkit-transform: translateX(-17em);
    -ms-transform: translateX(-17em);
    transform: translateX(-17em);
  }
}

/* XSmall */

@media screen and (max-width: 480px) {
  /* Basic */

  .PresentationPage,
  .PresentationPage html {
    min-width: 320px;
  }

  .PresentationPage,
  .PresentationPage input,
  .PresentationPage select,
  .PresentationPage textarea {
    font-size: 12pt;
  }

  /* List */

  .PresentationPage ul.actions {
    margin: 0 0 2.25em 0;
  }

  .PresentationPage ul.actions li {
    display: block;
    padding: 1.125em 0 0 0;
    text-align: center;
    width: 100%;
  }

  .PresentationPage ul.actions li:first-child {
    padding-top: 0;
  }

  .PresentationPage ul.actions li > * {
    width: 100%;
    margin: 0 !important;
  }

  .PresentationPage ul.actions li > *.icon:before {
    margin-left: -2em;
  }

  .PresentationPage ul.actions.small li {
    padding: 0.5625em 0 0 0;
  }

  .PresentationPage ul.actions.small li:first-child {
    padding-top: 0;
  }

  .PresentationPage ul.feature-icons li {
    display: block;
    width: 100%;
  }

  /* Button */

  .PresentationPage input[type="submit"],
  input[type="reset"],
  input[type="button"],
  .button {
    padding: 0;
  }
}
